import React from 'react';
import './Mainimg.css';


const Mainimg = () =>{
	return(
		<div className="container-fluid">
			<div className="row animated fadeInUp delay-1s">
				<div className="col-sm-6 col-lg-6"  data-aos="fade-right">
					<img className="logo grow" src={require('../img/back.jpg')} />
				</div>
				<div className="col-sm-6 col-lg-6 cus-div-m"  data-aos="fade-left">
					<h1 className="display-4 grow cus-head">Hello, I am <em>Salman Ahmed</em> currently settled in Pakistan</h1>
					<p className="lead cus-p grow">I have a degree in Electrical Engineering and also I am a <em>Certified Web Developer.</em> 
						I have been living in Pakistan since my childhood and now going on a work visa to Australia soon.</p>
				</div>
			</div>
		</div>
		);
}
export default Mainimg;