import React from 'react';
import './Progress.css'
const Progress = ()=>{
	return(
		<div className="container-fluid" align="center">
			<div className="row"  data-aos="fade-left" align="center">
				<div className="col-lg-3 grow">
					<h1 className="display-4 tc cus-pro-head">HTML/CSS</h1>
					<div align="center" class="progress-circle progress-95"><span>95</span></div>
				</div>
				<div className="col-lg-3 grow">
					<h1 className="display-4 tc cus-pro-head">JAVASCRIPT</h1>
					<div align="center" class="progress-circle progress-75"><span>75</span></div>
				</div>
				<div className="col-lg-3 grow">
					<h1 className="display-4 tc cus-pro-head">PHP</h1>
					<div align="center" class="progress-circle progress-70"><span>70</span></div>
				</div>
				<div className="col-lg-3 grow">
					<h1 className="display-4 tc cus-pro-head">REACT</h1>
					<div align="center" class="progress-circle progress-65"><span>65</span></div>
				</div>
			</div>
		</div>	
		);
}
export default Progress;